@charset "utf-8";
/* CSS Document */

@import "mdc.fab.css";
body,
td,
th,
p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
  line-height: 1.5;
}

ul,
li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
}

a {
  color: orange;
  text-decoration: none;
}

#skip-link a {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

#skip-link a:focus {
  position: static;
  width: auto;
  height: auto;
}

a:hover,
a:focus {
  color: #3397db;
  text-decoration: none;
}

a img {
  border: none 0px #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 20px;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
section {
  display: block;
}

header {
  position: relative;
  flex: 0 1 auto;
}

body {
  display: flex;
  height: 100vh;
  flex-direction: column;
  background-color: #fdfdfd;
  margin: 0;
  position: relative;
}

#maincontent {
  background-color: #f3f3f3;
  display: flex;
  flex-flow: column nowrap;
  flex: 1 1;
  position: relative;
  overflow-y: scroll;
}

#footer {
  position: relative;
  background-color: #444;
  color: #b8b5b5;
  font-size: 8pt;
  letter-spacing: 1px;
  padding: 15px;
  text-align: center;
  text-transform: uppercase;
}

/* ====================== Navigation ====================== */

nav {
  width: 100%;
  height: auto;
  /* min-height: 80px; */
  /* max-height: 80px; */
  background-color: #252831;
  text-align: left;
}

nav h1 {
  margin: auto;
  padding: 10px 0;
  text-align: center;
}

nav h1 a {
  color: #fff;
  font-size: 1em;
  font-weight: 200;
  text-transform: uppercase;
}

/* ====================== Map ====================== */

#map-container {
  position: relative;
  height: 40vh;
  min-height: 40vh;
}

#map {
  height: 100%;
  width: 100%;
  background-color: #ccc;
}

.inactive {
  display: none;
}

.blur {
  background: url('data:image/jpeg;base64,') 100%;
  background-size: cover;
  height: 100%;
  overflow: hidden;
  filter: blur(2px);
}

#image-blurred {
  position: relative;
  /* don't use height: 100% because cause problem with resolution > 850 (flex) */
}

.center {
  position: absolute;
  top: 50%;
  /* don't use height: 100% because cause problem with resolution > 850 (flex) */
  /* height: 100%; */
  width: 100%;
  padding-bottom: 50%;
  text-align: center;
  color: gray;
}

/* ====================== Restaurant Filtering ====================== */

.filter-options {
  background-color: #0000df;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.filter-options h2 {
  color: white;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1;
  margin: 10px 15px;
  display: block;
  flex: 2 100%;
}

.filter-options select {
  background-color: white;
  border: 1px solid #fff;
  font-family: Arial, sans-serif;
  font-size: 11pt;
  height: auto;
  letter-spacing: 0;
  margin: 10px;
  padding: 0 10px;
  width: 200px;
  flex: 1;
}

/* ====================== Restaurant Listing ====================== */

.restaurants {
  width: 100%;
}

#restaurants-list {
  background-color: #f3f3f3;
  list-style: outside none none;
  margin: 0 0 10px;
  padding: 0px 7px;
  text-align: center;
}

#restaurants-list li {
  background-color: #fff;
  border: 2px solid #ccc;
  font-family: Arial, sans-serif;
  margin: 10px;
  padding: 0 30px 65px;
  text-align: left;
  /* set the position relative to align the button on the bottom */
  position: relative;
}

#restaurants-list .restaurant-img {
  border-radius: 5px;
  background-color: #ccc;
  display: block;
  margin: 5px auto;
  width: 100%;
  height: auto;
}

#restaurants-list li h1 {
  color: #9c2900;
  font-family: Arial, sans-serif;
  font-size: 5vw;
  font-weight: 200;
  letter-spacing: 0;
  line-height: 1.3;
  margin: 20px 0 10px;
  text-transform: uppercase;
}

#restaurants-list p {
  margin: 0;
  font-size: 11pt;
}

#restaurants-list li button.button {
  background-color: #3634c5;
  border-bottom: 3px solid #eee;
  color: #fff;
  display: block;
  font-size: 10pt;
  padding: 8px 30px 10px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  /* set the position absolute to align the button on the bottom */
  position: absolute;
  bottom: 10px;
  /* center the button */
  left: calc(50% - 78.17px);
}

.app-fab--favorite {
  background-color: whitesmoke;
  color: grey;
  position: absolute;
  top: 1rem;
  right: 0.65rem;
}

.app-fab--isfavorite {
  color: red;
}

/* The toast */

#toast {
  visibility: hidden;
  width: calc(100% - 32px);
  background-color: #333;
  color: whitesmoke;
  text-align: left;
  font-weight: 400;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  bottom: 0px;
}

#toast.show {
  visibility: visible;
}

/* offline icon */

#offline {
  color: white;
  padding: 0 0 0 15px;
  display: none;
}

#offline.show {
  display: initial;
}